import { useEffect, useState } from "react";
import { useSnackbar } from "notistack";
import { useParams, useLocation, useNavigate } from "react-router-dom";
// @mui
import { Container } from "@mui/material";
// routes
import { PATH_DASHBOARD } from "../../routes/paths";
import { useDispatch } from "../../redux/store";
// hooks
import useSettings from "../../hooks/useSettings";
import { getCustomer, getCustomers } from "../../redux/slices/customer";
// components
import Page from "../../components/Page";
import HeaderBreadcrumbs from "../../components/HeaderBreadcrumbs";
// sections
import CustomerNewEditForm from "../../sections/@dashboard/customer/CustomerNewEditForm";
import extractErrorMessage from "../../utils/error";
import { useTranslation } from "react-i18next";
// ----------------------------------------------------------------------

export default function CustomerCreate() {
  const { themeStretch } = useSettings();

  const { t } = useTranslation();

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();

  const { pathname } = useLocation();

  const { customerKey = "" } = useParams();

  const isEdit = pathname.includes("edit");

  const [currentCustomer, setCurrentCustomer] = useState(null);

  useEffect(() => {
    if (isEdit) {
      handleGetObject();
    }
  }, [customerKey]);

  const handleGetObject = async () => {
    try {
      const result = await getCustomer(Number(customerKey));
      setCurrentCustomer(result);
      dispatch(getCustomers());
    } catch (error) {
      console.error(error);

      const errorMessage = extractErrorMessage(error);

      enqueueSnackbar(errorMessage, {
        variant: "error",
      });
      navigate(PATH_DASHBOARD.customer.root);
    }
  };

  return (
    <Page
      title={`Customer: ${
        !isEdit ? t("p_customer.new_customer") : t("p_customer.edit_customer")
      }`}
    >
      <Container maxWidth={themeStretch ? false : "lg"}>
        <HeaderBreadcrumbs
          heading={
            !isEdit
              ? t("p_customer.new_customer")
              : t("p_customer.edit_customer")
          }
          links={[
            { name: t("params.dashboard"), href: PATH_DASHBOARD.root },
            {
              name: t("sideBar.customer"),
              href: PATH_DASHBOARD.customer.root,
            },
            {
              name: !isEdit
                ? t("p_customer.new_customer")
                : `${t("p_customer.customer")} ${currentCustomer?.full_name}`,
            },
          ]}
        />

        <CustomerNewEditForm
          isEdit={isEdit}
          currentCustomer={currentCustomer}
        />
      </Container>
    </Page>
  );
}
