import PropTypes from "prop-types";
import * as Yup from "yup";
import { useEffect, useMemo } from "react";
import { useSnackbar } from "notistack";
import { useNavigate } from "react-router-dom";
// form
import { useForm, useFieldArray } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
// @mui
import { LoadingButton } from "@mui/lab";
import {
  Box,
  Card,
  Grid,
  Stack,
  Typography,
  Button,
  Divider,
  MenuItem,
} from "@mui/material";
// utils
import { fDateOnly } from "../../../utils/formatTime";
// routes
import { PATH_DASHBOARD } from "../../../routes/paths";
// components
import Iconify from "../../../components/Iconify";
import {
  FormProvider,
  RHFSelect,
  RHFTextField,
  RHFDateField,
  RHFPhoneField,
} from "../../../components/hook-form";
import { useDispatch, useSelector } from "../../../redux/store";
import {
  getGenres,
  getMaritals,
  getCountries,
  getTypeIds,
} from "../../../redux/slices/generale";
import {
  addCustomer,
  updateCustomer,
  addCustomerSuccess,
  updateCustomerSuccess,
} from "../../../redux/slices/customer";
import useIsMountedRef from "../../../hooks/useIsMountedRef";
import extractErrorMessage from "../../../utils/error";
import { useTranslation } from "react-i18next";

// ----------------------------------------------------------------------

CustomerNewEditForm.propTypes = {
  isEdit: PropTypes.bool,
  currentCustomer: PropTypes.object,
};

export default function CustomerNewEditForm({ isEdit, currentCustomer }) {
  const navigate = useNavigate();

  const { t } = useTranslation();

  const { enqueueSnackbar } = useSnackbar();

  const NationalIdShema = Yup.object().shape({
    type_id: Yup.string().required(
      t("field.required", {
        obj: t("field.type_id"),
      })
    ),
    number_id: Yup.string().required(
      t("field.required", {
        obj: t("field.number_id"),
      })
    ),
    created_on: Yup.string().required(
      t("field.required", {
        obj: t("field.created_on"),
      })
    ),
    expired_on: Yup.string().required(
      t("field.required", {
        obj: t("field.expired_on"),
      })
    ),
  });

  const NewCustomerSchema = Yup.object().shape({
    first_name: Yup.string().required(
      t("field.required", {
        obj: t("field.first_name"),
      })
    ),
    last_name: Yup.string().required(
      t("field.required", {
        obj: t("field.last_name"),
      })
    ),
    country: Yup.string().required(
      t("field.required", {
        obj: t("field.country"),
      })
    ),
    city: Yup.string().required(
      t("field.required", {
        obj: t("field.city"),
      })
    ),
    address: Yup.string().required(
      t("field.required", {
        obj: t("field.address"),
      })
    ),
    born_date: Yup.string().required(
      t("field.required", {
        obj: t("field.born_date"),
      })
    ),
    genre: Yup.string().required(
      t("field.required", {
        obj: t("field.genre"),
      })
    ),
    marital: Yup.string().required(
      t("field.required", {
        obj: t("field.marital"),
      })
    ),
    user_pieces_ids: Yup.array()
      .of(NationalIdShema)
      .min(1, "Must have at least one id")
      .max(3, "That is too many id"),
  });

  const defaultValues = useMemo(
    () => ({
      phone: currentCustomer?.phone || "",
      first_name: currentCustomer?.first_name || "",
      last_name: currentCustomer?.last_name || "",
      photo: currentCustomer?.photo || null,
      country: currentCustomer?.country || "",
      city: currentCustomer?.city || "",
      address: currentCustomer?.address || "",
      born_date: fDateOnly(currentCustomer?.born_date || new Date()),
      is_block: currentCustomer?.is_block || false,
      genre: currentCustomer?.genre || "",
      marital: currentCustomer?.marital || "",
      description: currentCustomer?.description || "",
      user_pieces_ids: currentCustomer?.user_pieces_ids?.map((item) => ({
        ...item,
        created_on: fDateOnly(item.created_on),
        expired_on: fDateOnly(item.expired_on),
      })) || [
        {
          type_id: "",
          number_id: "",
          created_on: new Date(),
          expired_on: new Date(),
        },
      ],
      keyId: currentCustomer?.id || -1,
    }),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [currentCustomer]
  );

  const methods = useForm({
    resolver: yupResolver(NewCustomerSchema),
    defaultValues,
  });

  const dispatch = useDispatch();

  const isMountedRef = useIsMountedRef();

  const {
    reset,
    watch,
    control,
    setError,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const values = watch();

  useEffect(() => {
    if (isEdit && currentCustomer) {
      reset(defaultValues);
    }
    if (!isEdit) {
      reset(defaultValues);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEdit, currentCustomer]);

  const onSubmit = async () => {
    const body = {
      ...values,
      born_date: fDateOnly(values.born_date),
      user_pieces_ids: values?.user_pieces_ids?.map((item) => ({
        ...item,
        created_on: fDateOnly(item.created_on),
        expired_on: fDateOnly(item.expired_on),
      })),
    };
    try {
      if (isEdit) {
        const results = await updateCustomer(body);
        dispatch(updateCustomerSuccess(results));
      } else {
        const results = await addCustomer(body);
        dispatch(addCustomerSuccess(results));
      }
      reset();
      enqueueSnackbar(!isEdit ? t("success.CREATE") : t("success.UPDATED"));
      navigate(PATH_DASHBOARD.customer.list);
    } catch (error) {
      console.error(error);

      const errorMessage = extractErrorMessage(error);

      enqueueSnackbar(t(`error.${errorMessage}`), {
        variant: "error",
      });
      if (isMountedRef.current) {
        if (error.response?.data?.phone) {
          setError("phone", { ...error, message: error.response?.data?.phone });
        }
        if (error.response?.data?.born_date) {
          setError("born_date", {
            ...error,
            message: error.response?.data?.born_date,
          });
        }
      }
    }
  };

  const { countries, genres, maritals, usertypeids } = useSelector(
    (state) => state.generale
  );

  useEffect(() => {
    dispatch(getGenres());
    dispatch(getMaritals());
    dispatch(getCountries());
    dispatch(getTypeIds());
  }, [dispatch]);

  const { fields, append, remove } = useFieldArray({
    control,
    name: "user_pieces_ids",
  });

  const handleAdd = () => {
    append({
      type_id: "",
      number_id: "",
      created_on: new Date(),
      expired_on: new Date(),
    });
  };

  const handleRemove = (index) => {
    remove(index);
  };

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: "grid",
                columnGap: 2,
                rowGap: 3,
                gridTemplateColumns: {
                  xs: "repeat(1, 1fr)",
                  sm: "repeat(2, 1fr)",
                },
              }}
            >
              <RHFPhoneField name="phone" label={t("field.phone")} />
              <RHFTextField name="first_name" label={t("field.first_name")} />
              <RHFTextField name="last_name" label={t("field.last_name")} />
              <RHFDateField
                name="born_date"
                maxDate={new Date()}
                label={t("field.born_date")}
              />
              <RHFSelect
                name="genre"
                label={t("field.genre")}
                placeholder={t("field.genre")}
              >
                <option value="" />
                {genres?.map((option) => (
                  <option key={option} value={option}>
                    {t(`genre.${option}`)}
                  </option>
                ))}
              </RHFSelect>

              <RHFSelect
                name="marital"
                label={t("field.marital")}
                placeholder={t("field.marital")}
              >
                <option value="" />
                {maritals?.map((option) => (
                  <option key={option} value={option}>
                    {t(`marital.${option}`)}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect
                name="country"
                label={t("field.country")}
                placeholder={t("field.country")}
              >
                <option value="" />
                {countries?.map((option) => (
                  <option key={option.id} value={option.id}>
                    {t(`country.${option.name}`)}
                  </option>
                ))}
              </RHFSelect>
              <RHFTextField name="city" label={t("field.city")} />
              <RHFTextField name="address" label={t("field.address")} />
            </Box>
            <Divider flexItem sx={{ borderStyle: "dashed", py: 2 }} />
            <Box sx={{ py: 3 }}>
              <Typography variant="h6" sx={{ color: "text.secondary", mb: 3 }}>
                {t("params.line_of_id")} :
              </Typography>

              <Stack
                divider={<Divider flexItem sx={{ borderStyle: "dashed" }} />}
                spacing={3}
              >
                {fields.map((item, index) => (
                  <Stack key={item.id} alignItems="flex-start" spacing={1.5}>
                    <Stack
                      sx={{
                        display: "grid",
                        columnGap: 2,
                        rowGap: 3,
                        gridTemplateColumns: {
                          xs: "repeat(1, 1fr)",
                          sm: "repeat(2, 1fr)",
                        },
                      }}
                    >
                      <RHFSelect
                        name={`user_pieces_ids[${index}].type_id`}
                        label={t("field.type_id")}
                        size="small"
                        InputLabelProps={{ shrink: true }}
                        SelectProps={{
                          native: false,
                          sx: { textTransform: "capitalize" },
                        }}
                      >
                        {usertypeids.map((option) => (
                          <MenuItem
                            key={option}
                            value={option}
                            sx={{
                              mx: 1,
                              my: 0.5,
                              borderRadius: 0.75,
                              typography: "body2",
                              textTransform: "capitalize",
                            }}
                          >
                            {t(`typeid.${option}`)}
                          </MenuItem>
                        ))}
                      </RHFSelect>

                      <RHFTextField
                        size="small"
                        name={`user_pieces_ids[${index}].number_id`}
                        label={t("field.number_id")}
                        InputLabelProps={{ shrink: true }}
                      />
                      <RHFDateField
                        size="small"
                        name={`user_pieces_ids[${index}].created_on`}
                        maxDate={new Date()}
                        label={t("field.created_on")}
                      />
                      <RHFDateField
                        size="small"
                        name={`user_pieces_ids[${index}].expired_on`}
                        minDate={new Date()}
                        label={t("field.expired_on")}
                      />
                    </Stack>

                    <Button
                      size="small"
                      color="error"
                      startIcon={<Iconify icon="eva:trash-2-outline" />}
                      onClick={() => {
                        const confirmation = window.confirm(
                          t("params.deletes_alert")
                        );

                        if (confirmation) {
                          handleRemove(index);
                        }
                      }}
                    >
                      {t("params.delete")}
                    </Button>
                  </Stack>
                ))}
              </Stack>

              <Divider sx={{ my: 3, borderStyle: "dashed" }} />

              <Stack
                spacing={2}
                direction={{ xs: "column-reverse", md: "row" }}
                alignItems={{ xs: "flex-start", md: "center" }}
              >
                <Button
                  size="small"
                  startIcon={<Iconify icon="eva:plus-fill" />}
                  onClick={handleAdd}
                  sx={{ flexShrink: 0 }}
                >
                  {t("params.add_new_id")}
                </Button>
              </Stack>
            </Box>

            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton
                type="submit"
                variant="contained"
                loading={isSubmitting}
              >
                {!isEdit
                  ? t("params.create", {
                      obj: t("p_customer.customer"),
                    })
                  : t("params.save_change")}
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
