const extractErrorMessage = (error) => {
  if (error.response?.data) {
    if (typeof error.response.data === "string") {
      // Si le message est directement une cha√Æne, utilisez-le comme message d'erreur
      return error.response.data;
    } else if (Array.isArray(error.response.data)) {
      // Si le message est un tableau, utilisez sa premi√®re entr√©e comme message d'erreur
      return error.response.data[0];
    } else if (error.response.data.detail) {
      // Si la cl√© 'detail' est pr√©sente, utilisez sa valeur comme message d'erreur
      return error.response.data.detail;
    }
  }

  // Utilisez un message g√©n√©rique si aucune correspondance n'est trouv√©e
  return "An error occurred. Please try again.";
};

export default extractErrorMessage;